nav{
    height: 5rem;
    width: 100vw;
    display: grid;
    background: var(--color-primary);
    place-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}

/*only shows medium and small device*/

.nav__toggle-btn{
    display: none;
}

.nav__container{
    display: flex;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.logo{
    width: 7rem;
    display: block;
}

.nav__links{
    display: flex;
    gap: 3.5rem;
    align-items: center;
}

.nav__links a{
    transition: var(--transition);
}

.nav__links a:hover{
    color: var(--color-secondary);
}

.active-nav{
    position: relative;
}

.active-nav::after{
    content: '';
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    background: var(--color-primary);
    position: absolute;
    left: calc(50% - 0.6rem) !important;
    transform: rotate(45deg);
    margin-top: 0.9rem;
}

/* Media queries (medium and small screen) */

@media screen and (max-width:1024px){
    .nav__toggle-btn{
        display: inline-block;
        font-size: 1.8rem;
        cursor: pointer;
        background: transparent;
    }
    .nav__toggle-btn svg{
        color: var(--color-gray-100);
    }
    .nav__links{
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        gap: 0;
        perspective: 400px;
    }

    .active-nav,.active-nav::after{
        display: none;
    }

    .nav__links li{
        height: 4rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.4);
        /*animation start*/
        animation: navAnimation 600ms ease forwards;
        transform: rotateX(90deg);
        opacity: 0;
        transform-origin: top;
    }

    .nav__links li:nth-child(2){
        animation-delay: 200ms;
    }
    .nav__links li:nth-child(3){
        animation-delay: 400ms;
    }
    .nav__links li:nth-child(4){
        animation-delay: 600ms;
    }
    .nav__links li:nth-child(5){
        animation-delay: 800ms;
    }
    .nav__links li:nth-child(6){
        animation-delay: 1s;
    }
    .nav__links li:nth-child(7){
        animation-delay: 1.2s;
    }

    @keyframes navAnimation {
        to{
            transform: rotateX(0);
            opacity: 1;
        }
    }


    .nav__links li a{
        background-color: var(--color-primary);
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 1rem 5rem 1rem 3rem;
    }

    .show__nav{
        display: flex;
    }

    .hide__nav{
        display: none;
    }
}

